body {
  background-color: plum;
  font-family: "Fredoka One";
}

.App {
  display: flex;
  text-align: center;
  flex-direction: column;
}

.Box {
  width: 3rem;
  height: 3rem;
  display: flex;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  user-select: none;
  margin: 0.25rem;
}

.OuterContainer {
  display: flex;
  justify-content: center;
}

.InnerContainer {
  width: 17.5rem;
  border-width: 1rem;
  border-style: double;
  border-radius: 5%;
  border-color: indigo;
  display: flex;
  flex-wrap: wrap;
}

.Timer {
  font-family: monospace;
  font-size: 2rem;
}
